.logIn-btn{
    background-color:transparent;
    width: 80%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    padding: 7% 12%;
    border:.1rem solid #182145;
    color: #182145;
    border-radius: 50rem;
    transition: all .2s ease-in;
    font-size: 1.4rem;
}
.googleIcon{
    color: #182145;
    font-size: 1.7rem;
    transition: all .3s ease-in;
}
.logIn-btn:hover{
    background-color: #182145;
    color: #fff;
}
